    -webkit-animation: rotates 2s infinite linear;
}

.up {
    width: 2.3vw;
    height: 3vw;
    left: 30%;
    top: 5%;
    overflow: hidden;
    background: #cef;
    position: absolute;
}

.up:before,
.up:after {
    content: "";
    width: 2.2vw;
    height: 2.2vw;
    background: #ecf0f1;
    position: absolute;
    z-index: 1;
}

.up:before {
    top: 55%;
    left: -55%;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
}

.up:after {
    top: 55%;
    left: 55%;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
}

