    background: #f1c40f;
    animation: mymove 2s infinite linear;
    -webkit-animation: mymove 2s infinite linear;
}

.down {
    width: 2.2vw;
    height: 3vw;
    left: 30.5%;
    bottom: 5%;
    overflow: hidden;
    background: #cef;
    position: absolute;
    z-index: 1;
}

.down:before,
.down:after {
    content: "";
    width: 2.2vw;
